<template>
  <div style="width:95%;margin:0 auto">
    <el-divider></el-divider>
    <el-card class="">
      <el-form :model="selectForm">
        <el-row :gutter="10">
          <el-col :span="8">
            <el-form-item label="():" >
              <el-select
                v-model="selectForm.season"
                remote
                filterable
                clearable
                :remote-method="remoteMethod"
                v-el-select-loadmore="loadMore()">
                <el-option
                  v-for="item in seasonArray.slice(0,rangeNumber)"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label=":" label-width="50px" style="width:90%">
              <el-input
                placeholder=""
                clearable
                v-model="selectForm.factorial"
                onkeyup="value=value.replace(/[^\d]/g,'')"
                @keyup.enter.native="mathFactorial">
              </el-input>
            </el-form-item>
            <span style="font-size:14px;color:red">{{factorialNum}}</span>
          </el-col>
          <el-col :span="10">
            <el-form-item label=":">
              <el-date-picker
                v-model="produceTimeRange"
                type="datetimerange"
                range-separator=""
                value-format="yyyy-MM-dd HH:mm:ss"
                start-placeholder=""
                end-placeholder="">
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <!--  -->
        <el-row>
          <el-col :span="24">
            <div style="float:right">
              <el-button type="primary" size="mini" @click="showViewsJSON">ViewsJSON</el-button>
              <el-button type="primary" size="mini" @click="showViewerJSON">ViewerJSON</el-button>
              <el-button type="primary" size="small" @click="handleSearch" v-throttle></el-button>
              <el-button type="primary" size="small"></el-button>
            </div>
          </el-col>
        </el-row>
      </el-form>
      <!-- Json -->
      <div class="json-view">
        <jsonViews :data="workJson" v-show="activeViews"></jsonViews>
        <JsonViewer copyable :expand-depth="5" :value="workJson" v-show="activeViewer"></JsonViewer>
      </div>
    </el-card>
  </div>
</template>

<script>
//
// import Vue from "vue"
// Vue.directive(
//   'el-select-loadmore', {
//     bind(el, binding) {
//       //element-uiscroll
//       const SELECTWRAP_DOM = el.querySelector('.el-select-dropdown .el-select-dropdown__wrap')
//       SELECTWRAP_DOM.addEventListener('scroll', function () {
//         const condition = this.scrollHeight - this.scrollTop <= this.clientHeight
//         if (condition) binding.value()
//       })
//     }
//   }
//
import {workJsons} from "@/common/js/workJson"
import jsonViews from 'vue-json-views' //jsonViews
import JsonViewer from 'vue-json-viewer' //JsonViewer
export default {
  name:"workbench",
  components:{
    jsonViews,
    JsonViewer
  },
  data(){
    return{
      workJson:null,
      activeViews:true,
      activeViewer:false,
      selectForm:{
        season:"",

      },
      factorialNum:"",
      seasonArray:[],
      rangeNumber:10, //
      produceTimeRange:[]
    }
  },
  watch:{
    "selectForm.factorial":{
      handler(newVal,oldVal){
        if(newVal === ""){
          this.factorialNum = ""
        }
      }
    }
  },
  created(){
    // let nowTimeDate = new Date();
    // let nowTime = nowTimeDate.toLocaleString()  //  '2022/7/25 15:53:36'
    // let realTime = nowTime.slice(0,5) + "0" + nowTime.slice(5)   //   '2022/07/25 15:53:36'
    // let now = realTime.replace(/\//g,"-");  //  /-     '2022-07-25 15:53:36'
    // console.log(nowTime,typeof nowTime === "string");
    // console.log(111,nowTime,now);

    this.workJson = workJsons
  },
  methods:{
    //
    remoteMethod(text){
      // console.log(text);
      //
      this.seasonArray = [] //
      for(let i=0;i<20000;i++){
        this.seasonArray.push({value:i,label:""+i})
      }

      //
      // let params = {
      //       seasonText:text
      //     }
      // get_season_list(params).then(res=>{
      //   if(res.data.code ==="0"){
      //     this.seasonArray = res.data.result;
      //   }else{
      //     this.$message.error(res.data.msg)
      //   }
      // }).catch(err=>{
      //   this.$message.error(err)
      // })
    },
    //
    loadMore(){
      return ()=> this.rangeNumber += 5 //5,
    },
    handleSearch(){
      let params = {
        season:this.selectForm.season,
        produceStartTime: this.produceTimeRange && this.produceTimeRange.length > 0 ?this.produceTimeRange[0] :"", //
        produceEndTime: this.produceTimeRange && this.produceTimeRange.length > 0 ?this.produceTimeRange[1] :"", //
      }
      // console.log("params",params);
      // console.log(11,this.produceTimeRange[0],typeof this.produceTimeRange[0]);
      console.log("");
    },
    showViewsJSON(){
      if(this.workJson === null || this.workJson === ""){
        this.activeViews = false
        this.activeViewer = false
        return false
      }
      this.activeViews = true
      this.activeViewer = false
    },
    showViewerJSON(){
       if(this.workJson === null || this.workJson === ""){
        this.activeViews = false
        this.activeViewer = false
        return false
      }
      this.activeViewer = true
      this.activeViews = false
    },
    //  
    mathFactorial(){
      function factorial(num){
        if(num <= 1){ //
          return 1
        }else{
          return num * factorial(num - 1)
        }
      }
      //
      this.factorialNum = factorial(this.selectForm.factorial)
    }
  }
}
</script>

<style lang="less" scoped>
.json-view{
  width: 90%;
  border:1px solid red;
  margin: 20px auto;
  min-height: 200px;
  max-height: 500px;
  overflow-x:hidden;
  overflow-y: scroll;
}
</style>



